.scanlines {
    overflow: visible;
    mix-blend-mode: difference;
    &::after {
        pointer-events: none;
        
        content: "";
        position: absolute;
        z-index: 2;

        width: 100%;
        height: 100%;
        
        top: 0;
        left: 0;
        
        background: repeating-linear-gradient(
            to bottom,
            transparent 0%,
             rgba(255, 255, 255, 0.05) .5%,
            transparent 1%
        );

        animation: fudge 7s ease-in-out alternate infinite;
    }
}